#container{
    width: 103em;
    height: auto;
    display: block;
}

.top-block {
    width: 100%;
    height: auto;
    box-sizing: content-box;
    padding: 6.5em 0 ;
    background: radial-gradient(#f00000a0,#220000,#000000);
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 1;
}

#content-box{
    width: 100%;
    height: auto;
    text-align: center;
}
#content-box p{
    font-size: 6em;
    margin-bottom: -4%;
}

#content-box p:last-child{
    font-size: 3em;
    margin : 5% 0;
}

.para-box{
    text-align: center;
    font-size: 1.5em;
    margin-top: -3%;
}

.email-box{
    width: 65%;
    height: 4em;
    margin-left: 18%;
    background: red;
    color: white;
    margin-bottom: 10%;
    display:flex;
}

.email-box div:first-child {
    width: 80%;
    height: 100%;
    font-size: 18px;
}

.email-box div input{
    width: 100%;
    height: 92%;
    background-color: #fff;
}

.email-box div:last-child{
    height: 100%;
    width: 24%;
    padding: 0 0.3em;
    margin-left: 0.6%;
    background: #22000082;
    font-size: 1.3em;
    text-transform: capitalize;
}

.email-box div:last-child:hover{
    background-color: #c91212;
    font-weight: bold;
}

.email-box div:last-child p{
    margin : 10% 12%;
    width: 150px;
    height: 30px;
    text-align: center;
}

.block{
    margin: 8em 0;
    display: flex;
}
.block .text-box{
    width: 30%;
    font-size: 20px;
    height: 100%;
    padding: 0 7em;
    margin: 0 2em;
}
.block div p:first-child {
    font-size: 2.5em;
}
.media-block{
    width: 31.3em;
    height: 12.5em;
    /* overflow: hidden; */
    background-color: #ffffff;
    box-shadow: 0 0 15px  #f2000054;
}
.media-block img{
    width: 31.3em;
    height: 12.5em;
}

.mb-2, .mb-4{
    margin: 3em 8em;
}

.hr-line, #footer-line{
    width: 90%;
    height: 5px;
    background: #ffffff05;
}

.block6{
    display: block;
}
.block6 .ques-box{
    margin-left: 22%;
    width: 80%;
}

.block6 div ul{
    width: 60%;
    font-size: 1.2em;
    list-style: none;
    text-align: left;
}

.block6 div ul li{
    width: 100%;
    height: 1.4em;
    padding: 0.5em;
    margin: 0.3em 0;
    background-color: #42424276;
}

.block6 div ul li span{
    float: right;
    font-size: 1.2em;
    margin: 0 10px 0 0;
}
 .block6 #box-email .para-box{
    text-align: center;
    font-size: 0.7em;
    margin-top: 5%;
}
.block6 #box-email .email-box div:first-child{
    width: 75%;
    height: 100%;
    background-color: #fff;
    overflow: visible;
}

.block6 #box-email .email-box div:first-child input{
    width: 100%;
    height: 90%;
    background-color: #fff;
}

.block6 #box-email .email-box div:last-child{
    height: 100%;
    width: 25%;
    margin-left: 0.8%;
    background: #22000082;
    font-size: .5em;
    padding: 0 2em;
    text-transform: capitalize;
}

.block6 #box-email .email-box div:last-child:hover{
    background-color: #c91212;
    font-weight: bold;
}