header{
    width: 99%;
    height: 5em;
    display: inline-block;
    position: fixed;
    background-color: #efefef10;
}

header div:first-child{
    font-size: 2.8em;
    text-transform: uppercase;
    color: red;
    float: left;
    text-shadow: 6px 5px 5px #12121298;
    font-weight: bold;
    margin: 1% 0 0 2%;
}
header div input{
    height: 2.3em;
    padding: 0 5%;
    background-color: #fff;
    margin-right: 5%;
}

header div:last-child{
    float: right;
    width: 300px;
    height: 30px;
    margin: 1%;
    padding: 0.1%;
}

header div input:last-child{
    width: 5em;
    font-size: 1em;
    padding: 0 1em;
    background-color: #524f4f87;
    text-transform: capitalize;
}

header div input:last-child:hover {
    background-color: #75727250;
    color:#fefefe;
    font-weight: bold;
}